.modal {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 10;

  .modal_container {
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    margin: auto;
    min-width: 400px;
    width: fit-content;
    height: fit-content;
  }

  .modal_content {
    display: flex;
    flex-direction: column;
    width: 100%;
    padding: 20px 20px 30px 20px;
    border: 1px solid #ccc;
    border-radius: 12px;
    background: white;
  }

  .close {
    position: absolute;
    display: inline-flex;
    cursor: pointer;
    right: 20px;
    top: 20px;
  }

  .dim {
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.3)
  }
}